@import "./styles/null.scss";

@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900&display=swap");
/*-----------------------------------------------------------------------------------------------------*/

body {
   font-size: 14px;
   color: #2d2d2d;
   font-family: "Montserrat", sans-serif;
}

.wrapper {
   min-height: 100vh;
   width: 100%;
   overflow: hidden;
   display: flex;
   flex-direction: column;
   align-items: center;
   padding: 30px 0 50px 0;
}

.wrapper._dark {
   background-color: #0d1117;
   transition: 0.3s;
   .header {
      .header__container {
         .header__actions {
            .react-select {
               .react-select__control {
                  background-color: #4f4f4f;
                  font-weight: 500;
                  color: #fff;
                  border: 2px solid transparent;
                  font-size: 16px;
                  border-radius: 10px;
                  cursor: pointer;
                  transition: 0.3s;
                  box-shadow: none;
                  &:hover {
                     box-shadow: none;
                     border: 2px solid #4793ff;
                     // border-color: transparent;
                  }
                  .react-select__value-container {
                     padding: 0 0 0 5px;
                     .react-select__single-value {
                        color: #fff;
                     }
                     .react-select__placeholder {
                        color: #fff;
                     }
                  }
                  .react-select__indicators {
                     svg {
                        color: #fff;
                        transition: 0.3s;
                     }
                     .react-select__indicator-separator {
                        display: none;
                     }
                  }
               }
               .react-select__menu {
                  color: hsl(0, 0%, 50%);
                  font-weight: 500;
                  border: 0px;
                  font-size: 16px;
                  border-radius: 10px;
                  overflow: hidden;
                  .react-select__option {
                     cursor: pointer;
                  }
               }
            }
         }
      }
   }
   .today-weather {
      background-color: #4f4f4f;
      box-shadow: 0px 0px 8px 1px rgba(255, 255, 255, 0.2);
      .today-weather__date {
         color: #fff;
      }
   }
   .today-parameters {
      background-color: #4f4f4f;
      box-shadow: 0px 0px 8px 1px rgba(255, 255, 255, 0.2);
      .today-parameters__item-container {
         background-color: #ffffff;
      }
      .today-parameter__item-description {
         color: #fff;
      }
   }
   .longterm__tabs-item {
      background-color: #4f4f4f;
      color: #fff;
      box-shadow: 0px 0px 8px 1px rgba(255, 255, 255, 0.1) !important;
   }
   .longterm__cancel {
      background-color: #4f4f4f;
      color: #fff;
      box-shadow: 0px 0px 8px 1px rgba(255, 255, 255, 0.1) !important;
   }
   .longterm__days {
      background-color: #4f4f4f;
      box-shadow: 0px 0px 8px 1px rgba(255, 255, 255, 0.2);
      .longterm-item {
         background-color: #2e3035;
      }
      .longterm-item__date-day {
         color: #ffffff;
      }
      .longterm-item__degrees-day {
         color: #ffffff;
      }
   }
}

.header {
   max-width: 100%;
   width: 100%;
   padding: 0 15px;
   margin: 0 0 50px 0;
   .header__container {
      max-width: 1200px;
      width: 100%;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      .header__logo {
         // border: 1px solid black;
         display: flex;
         align-items: center;
         .header__logo-img {
            margin-right: 10px;
         }
         .header__logo-txt {
            font-weight: 700;
            text-transform: uppercase;
            color: #4793ff;
            font-size: 25px;
         }
      }
      .header__actions {
         max-width: 250px;
         width: 100%;
         display: flex;
         align-items: center;
         justify-content: space-between;
         // border: 1px solid black;
         .header__theme {
            cursor: pointer;
         }
         .header__sort {
            width: 190px;
            height: 37px;
            // border: 1px solid red;
         }
      }
   }
}

.main {
   max-width: 100%;
   width: 100%;
   padding: 0 15px;
}

.today {
   max-width: 100%;
   width: 100%;
   margin-bottom: 50px;
   .today__container {
      max-width: 1200px;
      width: 100%;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
   }
}

.today-weather {
   max-width: 400px;
   width: 100%;
   padding: 20px;
   box-shadow: 0px 0px 8px 0px rgba(34, 34, 34, 0.1);
   border-radius: 20px;
   margin-right: 45px;
   transition: 0.3s;
   .today-weather__top {
      display: flex;
      justify-content: space-between;
      margin-bottom: 25px;
      .today-weather__text {
         display: flex;
         flex-direction: column;
         align-items: center;
         .today-weather__degrees {
            font-weight: 500;
            font-size: 96px;
            color: #4793ff;
            position: relative;
            // border: 1px solid red;
            line-height: 70px;
            margin-bottom: 15px;
         }
         .today-weather__date {
            font-size: 40px;
            font-weight: 400;
            transition: 0.3s;
         }
      }
      .today-weather__icon-container {
         max-width: 119px;
         width: 100%;
         height: 119px;
         .today-weather__icon {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transform: scale(1.2, 1.2);
         }
      }
   }
   .today-weather__bottom {
      .today-weather__time {
         font-size: 25px;
         color: #939cb0;
         margin-bottom: 10px;
      }
      .today-weather__city {
         font-size: 25px;
         color: #939cb0;
      }
   }
}

.today-parameters {
   max-width: 750px;
   width: 100%;
   padding: 20px;
   box-shadow: 0px 0px 8px 0px rgba(34, 34, 34, 0.1);
   border-radius: 20px;
   background: url("../public/images/cloud.png") 100% 0 no-repeat;
   transition: 0.3s;
   .today-parameters__list {
      .today-parameters__item {
         display: flex;
         align-items: center;
         .today-parameters__item-container {
            width: 38px;
            height: 38px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow: 4px 4px 8px 0px rgba(71, 147, 255, 0.15);
            margin-right: 20px;
            transition: 0.3s;
            .today-parameters__item-img {
            }
         }
         .today-parameter__item-parameter {
            margin-right: 20px;
            font-size: 16px;
            color: #939cb0;
         }
         .today-parameter__item-description {
            font-size: 16px;
            font-weight: 500;
            transition: 0.3s;
         }
         &:not(:last-child) {
            margin-bottom: 20px;
         }
      }
   }
}

.longterm {
   max-width: 100%;
   width: 100%;
   .longterm__container {
      max-width: 1200px;
      width: 100%;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
   }
}

.longterm__actions {
   max-width: 100%;
   width: 100%;
   display: flex;
   justify-content: space-between;
   margin-bottom: 10px;
   .longterm__tabs {
      .longterm__tabs-list {
         display: flex;
         .longterm__tabs-item {
            padding: 9px 10px;
            box-shadow: 4px 4px 8px 0px rgba(0, 0, 0, 0.1);
            font-size: 16px;
            border-radius: 5px;
            cursor: pointer;
            transition: 0.3s;
            &:hover {
               background-color: #4793ff;
               color: #ffffff;
            }
            &:active {
               transform: translateY(5px);
            }
            &:not(:last-child) {
               margin-right: 30px;
            }
         }
      }
   }
   .longterm__cancel {
      padding: 7px 10px;
      box-shadow: 4px 4px 8px 0px rgba(0, 0, 0, 0.1);
      font-size: 16px;
      border-radius: 5px;
      cursor: pointer;
      transition: 0.3s;
      background-color: transparent;
      &:hover {
         background-color: #2d2d2d;
         color: #ffffff;
      }
      &:active {
         transform: translateY(5px);
      }
   }
}

.longterm__days {
   max-width: 100%;
   width: 100%;
   display: flex;
   justify-content: flex-start;
   flex-wrap: wrap;
   padding: 20px;
   transition: 0.3s;
   border-bottom-left-radius: 20px;
   border-bottom-right-radius: 20px;
   .longterm-item {
      max-width: 150px;
      width: 100%;
      display: flex;
      flex-direction: column;
      background-color: #4793ff33;
      border-radius: 10px;
      padding: 10px;
      // margin-bottom: 20px;
      transition: 0.3s;
      .longterm-item__date {
         margin-bottom: 12px;
         .longterm-item__date-day {
            font-weight: 500;
            font-size: 18px;
            margin-bottom: 7px;
            transition: 0.3s;
         }
         .longterm-item__date-number {
            color: #939cb0;
         }
      }
      .longterm-item__icon-container {
         max-width: 35px;
         min-height: 35px;
         margin: 0 0 13px 3px;
         // border: 1px solid black;
         .longterm-item__icon {
            width: 100%;
            height: 100%;
            object-fit: contain;
            // border: 1px solid red;
            transform: scale(1.6, 1.8);
         }
      }
      .longterm-item__degrees {
         .longterm-item__degrees-day {
            font-weight: 500;
            font-size: 18px;
            margin-bottom: 7px;
            transition: 0.3s;
         }
         .longterm-item__degrees-night {
            margin-bottom: 7px;
            color: #939cb0;
            font-size: 13px;
         }
         .longterm-item__degrees-weather {
            color: #939cb0;
            font-size: 13px;
         }
      }
      &:not(:last-child) {
         margin-right: 18px;
      }
   }
}

.react-select {
   .react-select__control {
      background-color: #4793ff33;
      font-weight: 500;
      color: hsl(0, 0%, 50%);
      border: 2px solid transparent;
      font-size: 16px;
      border-radius: 10px;
      cursor: pointer;
      transition: 0.3s;
      box-shadow: none;
      &:hover {
         box-shadow: none;
         // border-color: transparent;
      }
      .react-select__value-container {
         padding: 0 0 0 5px;
         .react-select__single-value {
            color: hsl(0, 0%, 50%);
         }
      }
      .react-select__indicators {
         svg {
            color: hsl(0, 0%, 50%);
            transition: 0.3s;
         }
         .react-select__indicator-separator {
            display: none;
         }
      }
   }
   .react-select__menu {
      color: hsl(0, 0%, 50%);
      font-weight: 500;
      border: 0px;
      font-size: 16px;
      border-radius: 10px;
      overflow: hidden;
      .react-select__option {
         cursor: pointer;
      }
   }
}

.loader {
   height: 500px;
   opacity: 1;
   .loader_img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      animation-name: loader_animation;
      animation-duration: 75s;
      animation-timing-function: linear;
      animation-iteration-count: infinite;
   }
}

@keyframes loader_animation {
   0% {
      transform: rotate(0);
   }
   100% {
      transform: rotate(180deg);
   }
}
